.law-firm-home-section-1,
.law-firm-section-header,
.law-firm-single-news-section-1 {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .overlay {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0.96;
        background: #152833;
        z-index: 1;
    }
    h1 {
        color: var(--White, #fff);
        font-size: 80px;
        font-weight: 400;
        line-height: 84px;
        letter-spacing: -1.6px;
    }
    .bg-circle {
        width: 650px;
        height: 650px;
        flex-shrink: 0;
        border-radius: 650px;
        background: var(--tc-theme-primary);
        z-index: -2;
    }
}
.banner-img {
    margin-top: -170px;
}
.law-firm-home-section-2 {
    svg {
        margin-top: -10px;

        text {
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: -0.8px;
            fill: transparent;
            stroke: var(--tc-theme-primary);
            stroke-width: 2px;
            animation: animated-stroke 2s;
            text-anchor: middle;
            dominant-baseline: middle;
        }
    }
    .marketing-info {
        h4 {
            margin-top: -40px;
            margin-bottom: 24px;
        }
        p {
            margin-bottom: 60px;
        }
    }
}
.card-service {
    .card-btn {
        transform: translateY(-100%);
        transition: all 0.4s ease-in-out;
        z-index: 1000;
        position: relative;
        opacity: 0;
    }
    &:hover {
        .card-btn {
            transform: translateY(0%);
            transition: all 0.4s ease-in-out;

            opacity: 1;
        }
    }
}
.law-firm-home-section-4 {
    // accordion
    .accordion {
        .card {
            background: transparent;
            transition: all 0.3s linear;
            .card-header {
                background: transparent;
                a {
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    background-color: transparent;
                    transition: all 0.3s ease-in-out;
                    .arrow {
                        &::before {
                            transition: 0.3s;
                            transform: rotate(0deg);
                            font-family: remixicon;
                            content: '\f1af';
                            color: var(--tc-theme-primary);
                            font-size: 14px;
                            font-weight: 300;
                        }
                    }
                    &.collapsed {
                        background-color: transparent;
                        .arrow {
                            background-color: transparent;
                            &::before {
                                transition: 0.3s;
                                color: var(--tc-system-black);
                                transform: rotate(180deg);
                                content: '\ea13';
                            }
                        }
                    }
                }
            }
            .collapse {
                transition: all 0.3s linear;
                &.show {
                    border-bottom: 0;
                }
            }
        }
        .collapse-custom {
            border-radius: 0px;
            transition: all 0.3s linear;
            &.active {
                background: linear-gradient(180deg, #fff 0%, #f6f3ef 113.64%);
            }
        }
    }
}
.avatar-2 {
    margin-left: -1rem;
}
.card-team {
    &:hover {
        .team-overlay {
            opacity: 1;
            transform: translateX(0%);
        }
    }
    .team-overlay {
        content: '';
        position: absolute;
        transition: all 0.3s linear;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: var(--tc-system-gold);
        opacity: 0;
        transform: translateX(-50%);
        .icon {
            position: relative;
            width: 34px;
            height: 34px;
            border-radius: 34px;
            svg {
                height: 14px;
                width: 14px;
                fill: var(--tc-system-white);
                opacity: 0.5;
            }
            &:hover {
                background: #292929;
                svg {
                    fill: var(--tc-system-white);
                    opacity: 1;
                    transition-duration: 0.3s;
                }
            }
        }
    }
}
.law-firm-home-section-8 {
    .circle-text-2 {
        position: absolute;
        margin-bottom: 0;
        font-size: 15px;
        text-transform: uppercase;
        left: 50%;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        span {
            position: absolute;
            top: -74px;
            display: inline-block;
            transform-origin: 0 74px;
        }
    }
    .circle-1 {
        width: 262px;
        height: 262px;
        flex-shrink: 0;
        border-radius: 262px;
        border: 1px solid var(--tc-system-white);
        opacity: 0.1;
    }
    .circle-2 {
        width: 388px;
        height: 388px;
        border-radius: 388px;
        border: 1px solid var(--tc-system-white);
        opacity: 0.04;
    }
}
.section-footer-12 {
    background: var(--tc-system-gold);
    .icon {
        svg {
            height: 14px;
            width: 14px;
            fill: #dcc69f;
        }
        &:hover {
            svg {
                fill: var(--tc-system-white);
                opacity: 1;
                transition-duration: 0.3s;
            }
        }
    }
}
.input-group {
    .form-control {
        color: var(--tc-system-black);
        &:hover,
        &:focus {
            background-color: var(--tc-system-white) !important;
            border: 1px solid var(--tc-system-black) !important;
        }
    }
}

.law-firm-single-service-section-1 {
    .input-group {
        .form-control {
            color: var(--tc-system-black);
            background-color: var(--tc-system-white) !important;
            &:focus {
                background-color: var(--tc-system-white) !important;
            }
        }
    }
    // accordion
    .accordion {
        .card {
            background: transparent;
            transition: all 0.3s linear;
            .card-header {
                background: transparent;
                a {
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    background-color: var(--tc-system-white);
                    transition: all 0.3s linear;
                    .arrow {
                        &::before {
                            transition: 0.3s;
                            transform: rotate(0deg);
                            font-family: remixicon;
                            content: '\f1af';
                            color: var(--tc-system-black);
                            font-size: 14px;
                            font-weight: 300;
                        }
                    }
                    &.collapsed {
                        background-color: transparent;
                        .arrow {
                            background-color: transparent;
                            &::before {
                                transition: 0.3s;
                                color: var(--tc-system-black);
                                transform: rotate(180deg);
                                content: '\ea13';
                            }
                        }
                    }
                }
            }
        }
    }
}
.pagination_item {
    background-color: #f6f3ef;
    border-radius: 0px !important;
    font-size: 14px !important;
    font-weight: 500 !important;
    &.active {
        background-color: var(--tc-system-gold);
        color: var(--tc-system-white) !important;
    }
    &:hover {
        background-color: var(--tc-system-gold);
        color: var(--tc-system-white) !important;
    }
    &:focus {
        background-color: var(--tc-system-gold) !important;
        color: var(--tc-system-white) !important;
    }
}
